body {
  margin: 40px 0 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scroll-behavior: smooth;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

section {
  padding: 60px 0 20px;
}

#profile-photo {
    text-align: center;
}

#profile-photo img {
    width: 25em;
    border-radius: 100%;
    margin: 20px 0;
}

#profile-photo figcaption {
    text-align: justify;
}

#curriculum-button {
    padding: 10px 30px;
    background-color: black;
    color: white;
    border: 1px solid black;
    border-radius: 20px;
}

#curriculum-button:hover {
    background-color: white;
    color: black;
    cursor: pointer;
}

h1, h3{
	font-weight:bold;
}
#skills {
    text-align: center;
	background: #343a40;
	color:#fff;
}

#list-skills {
    padding: 20px 0;
	max-width:600px;
}

#list-skills {
    list-style: none;
	
}

#list-skills a {
    padding: 10px 20px;
    margin: 20px;
    background-color: darkgrey;
    border-radius: 5px;
    cursor: pointer;
}

#list-skills a:hover {
    background-color: black;
    color: white;
}
img {
    width: 250px;
}

figcaption , h2{
	font-weight:bold;
}

#blog {
    text-align: center;
}

#blog a {
    text-decoration: none;
}

#blog figcaption {
    padding: 20px 0 10px;
    text-align: justify;
    color: black;
}

#blog article {
    color: black;
  border-radius: 10px;
}

@-webkit-keyframes expand{
	from {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
	to {
		-webkit-transform: scale(1.35);
		        transform: scale(1.35);
	}
}

@keyframes expand{
	from {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
	to {
		-webkit-transform: scale(1.35);
		        transform: scale(1.35);
	}
}

#blog article:hover{
    color: white;
    -webkit-animation-name: expand;
            animation-name: expand;
    -webkit-animation-duration: 0.2s;
            animation-duration: 0.2s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

#portfolio {
    text-align: center;
	background:#343a40;
	color:#fff;
}

#portfolio a {
    margin: 5px 10px;
}

#port{
	color:#000;;
}
#infos h2,
#infos-github {
    text-align: center;
}

#infos-github {
    padding: 10px 0;
    font-size: 0.8em;
}

#infos p {
    text-align: justify;
}

#quote {
    font-size: 0.8em;
    font-style: italic;
}

#quote-author {
    font-size: 0.9em;
    text-align: end;
}

.medium-icon {
    width: 4rem;
}

figure {
    margin-top: 20px;
}

h4{
	font-weight:bold;
}
#contato {
    text-align: center;
}

.icones {
    width: 2em;
}

#redes-sociais a {
    display: inline-block;
    margin: 0 5px;
	
}

h1 {
    margin: 40px 0 0;
	text-align:center;
	padding-bottom: 20px;
}


#Contato{
	background: #ccc;
}
